import React from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// core components
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import Button from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardAvatar from "components/Card/CardAvatar.js";
import CardBody from "components/Card/CardBody.js";
import IconButton from "@material-ui/core/IconButton";

import { useDispatch } from "react-redux";
import { addProduct } from "store/action";

const styles = {
  cardTitle: {
    padding: "15px",
    display: "flex",
    justifyContent: "center",
    position: "relative",
    marginTop: "30px",
    maxHeight: "25px",
    minHeight: "75px",
    textAlign: "center !important",
  },
  cardCategoryWhite: {
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0",
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
  },
  display: {
    display: "flex",
    justifyContent: "grid",
  },
  price: {
    display: "flex",
    justifyContent: "center",
  },
  pricePosition: {
    marginLeft: "25px",
  },
};

const useStyles = makeStyles(styles);

const useStyles2 = makeStyles((theme) => ({
  container2: {
    marginTop: "75px",
    marginLeft: "0px",
    maxWidth: "330px",
    minWidth: "330px",
    padding: "0px",
    display: "flex",
    maxHeight: "390px",
    minHeight: "390px",
    justifyContent: "center",
    [theme.breakpoints.up("md")]: {
      marginTop: "75px",
      maxWidth: "1100px",
      minWidth: "1100px",
      padding: "0px",
      maxHeight: "400px",
      minHeight: "400px",
    },
  },
  description: {
    margin: " auto -1",
    marginLeft: "-15px",
    maxWidth: "300px",
    minWidth: "300px",
    minHeight: "50px",
    maxHeight: "50px",
    color: "black",
    textAlign: "center !important",
    display: "flex",
    justifyContent: "center",
    [theme.breakpoints.up("md")]: {
      margin: "1.071rem auto 0",
      maxWidth: "300px",
      minWidth: "300px",
      minHeight: "50px",
      maxHeight: "50px",
      color: "black",
      textAlign: "center !important",
    },
  },
}));

export default function CardDetailProduct({ setChange, product }) {
  const classes = useStyles();
  const classes2 = useStyles2();

  const dispatch = useDispatch();

  function arrowBack() {
    setChange("Product");
  }
  function addToCart(product) {
    dispatch(addProduct(product));
    window.scrollTo(0, 0);
  }

  return (
    <div>
      <IconButton onClick={()=>{arrowBack()}} className={classes2.arrowResponsive}>
        <ArrowBackIcon />
      </IconButton>

      <GridContainer className={classes.container}>
        <GridItem xs={12} sm={12} md={8}>
          <Card className={classes2.container2} >
            <CardAvatar profile>
              <img className={classes2.cardImg} src={product.img} alt="..." />
            </CardAvatar>
            <CardBody >
              <h4 className={classes.cardTitle}>{product.product}</h4>
              <p className={classes2.description}>{product.description}</p>
              <div className={classes.price}>
                <Button
                  onClick={() => {
                    addToCart(product);
                  }}
                >
                  Agregar al carrito
                </Button>
                <h2 className={classes.pricePosition}>${product.price}</h2>
              </div>
            </CardBody>
          </Card>
        </GridItem>
      </GridContainer>
    </div>
  );
}
