import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import Typography from "@material-ui/core/Typography";
import AddShoppingCartIcon from "@material-ui/icons/AddShoppingCart";

const useStyles = makeStyles({
  root: {
    maxWidth: 285,
    margin: "15px",
    minWidth: "285px",
    maxHeight: "255px",
    minHeight: "255px",
  },
  icons: {
    display: "flex",
    justifyContent: "flex-start",
  },
  buyIcon: {
    marginTop: '15px',
    marginLeft: '200px',
  },
  price:{
    minWidth: "25px",
  }
});

export default function CardProduct({ product, onProduct, viewDetails }) {
  const classes = useStyles();
  function onsubmit() {
    onProduct(product);
    viewDetails();
    window.scrollTo(0, 0);
  }
  return (
    <Card className={classes.root}>
      <CardActionArea onClick={()=>{onsubmit()}}>
        <CardMedia
          component="img"
          alt={product.product}
          height="140"
          image={product.img}
          title={product.product}
        />
        <CardContent>
          <Typography variant="h6" component="h1">
            {product.product}
          </Typography>
          <Typography variant="body2" color="textSecondary" component="p">
            {product.description}
          </Typography>
          <div className={classes.icons}>
            <Typography className={classes.price} variant="h6" color="textPrimary" component="p">
              ${product.price}
            </Typography>
            <div className={classes.buyIcon}>
                <AddShoppingCartIcon />
            </div>
          </div>
        </CardContent>
      </CardActionArea>
    </Card>
  );
}
