import React from "react";
import Badge from '@material-ui/core/Badge';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import IconButton from '@material-ui/core/IconButton';
import { useSelector } from "react-redux";

export default function HeaderLinks({ setCart }) {

  const products = useSelector(state=>state.products)
  const countProduct = products.map(product=>product.quantity)
  const totalCart = countProduct.reduce((a,b)=>a+b,0)
  
  return (
    <div>
      <IconButton onClick={() => setCart('Cart')} aria-label="notifications" color="inherit">
        <Badge badgeContent={totalCart} color="secondary">
          <ShoppingCartIcon fontSize="large" />
        </Badge>
      </IconButton>
    </div>
  );
}
