import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import DeleteOutlineIcon from "@material-ui/icons/DeleteOutline";
import IconButton from "@material-ui/core/IconButton";
import { removeProduct } from "store/action";
import ButtonQuantity from "./ButtonQuantity/ButtonQuantity";
import { useDispatch } from "react-redux";

const useStyles = makeStyles({
  tableMargin: {
    marginTop: "15px",
  },
  table: {
    minWidth: 650,
  },
  cardImg: {
    minWidth: "150px",
    maxWidth: "150px",
    minHeight: "85px",
    maxHeight: "85px",
  },
  button: {
    marginLeft: 25,
  },
});

function CartList({ product }) {
  const dispatch = useDispatch();
  const classes = useStyles();

  return (
    <TableRow key={product.product}>
      <TableCell>
        <IconButton onClick={() => dispatch(removeProduct(product))}>
          <DeleteOutlineIcon />
        </IconButton>
        {product.product}
      </TableCell>
      <TableCell>
        <ButtonQuantity product={product} />
      </TableCell>
      <TableCell></TableCell>
      <TableCell align="right">
        <img className={classes.cardImg} src={product.img} alt="..." />
      </TableCell>
      <TableCell align="right">{product.category}</TableCell>
    </TableRow>
  );
}

export default CartList;
