import CardCategory from "components/Card/CardCategory";
import CardDetailProduct from "components/Card/CardDetailProduct";
import React, { useState } from "react";
import categories from "assets/categories";
import CatalogProduct from "components/Catalog/CatalogProduct";
import IconButton from "@material-ui/core/IconButton";

function Catalog(props) {
  const [change, setChange] = useState("Category");
  const [categoryProduct, setCategoryProduct] = useState("");
  const [product, setProduct] = useState("");

  if (change === "Category") {
    return categories.map((category) => {
      return (
        <div key={category.category}>
          <IconButton
          >
          </IconButton>

          <CardCategory
            setCategoryProduct={setCategoryProduct}
            setChange={setChange}
            category={category}
          />
        </div>
      );
    });
  }
  if (change === "Product") {
    return (
      <CatalogProduct
        setChange={setChange}
        setProduct={setProduct}
        categoryProduct={categoryProduct}
      />
    );
  }
  if (change === "ProductDetail") {
    return <CardDetailProduct product={product} setChange={setChange} />;
  }
}

export default Catalog;
