export const ADD_PRODUCT = "ADD_PRODUCT";
export const REMOVE_PRODUCT = "REMOVE_PRODUCT";
export const REMOVE_ALL_PRODUCT = "REMOVE_ALL_PRODUCT";
export const DECREASE_QUANTITY = "DECREASE_QUANTITY";
export const INCREASE_QUANTITY = "INCREASE_QUANTITY";

export function addProduct(payload) {
  //console.log("payload",payload)
  if (payload.inCart === false) {
    payload.inCart = true;
    return (dispatch) => dispatch({ type: "ADD_PRODUCT", payload });
  } else {
    return (dispatch) => dispatch({ type: "", payload });
  }
}
export function removeProduct(payload) {
  return (dispatch) => dispatch({ type: "REMOVE_PRODUCT", payload });
}

export function setQuantityProduct(product, quantity) {
  return (dispatch) =>
    dispatch({ type: "SET_QUANTITY_PRODUCT", payload: { quantity, product } });
}

export function decreaseQuantity(product){
  //console.log(product)
  if (product.quantity > 1) {
    product.quantity--;
    product.total = product.price * product.quantity;
    return (dispatch) => dispatch({ type: "DECREASE_QUANTITY", payload:product });
  }else {
    return (dispatch) => dispatch({ type: "", payload:product });
  }
}

export function increaseQuantity(product){
  //console.log(product)
  if (product.quantity < 100) {
    product.quantity++;
    product.total = product.price * product.quantity;
    return (dispatch) => dispatch({ type: "INCREASE_QUANTITY", payload:product });
  }else {
    return (dispatch) => dispatch({ type: "", payload:product });
  }
}
