import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import CartList from "components/Cart/CartList";

import { useSelector } from "react-redux";

import Button from "components/CustomButtons/Button.js";

const useStyles = makeStyles({
  tableMargin: {
    marginTop: "15px",
  },
  table: {
    minWidth: 650,
  },
  cardImg: {
    minWidth: "150px",
    maxWidth: "150px",
    minHeight: "85px",
    maxHeight: "85px",
  },
  button: {
    marginLeft: 25,
  },
});

export default function BasicTable() {
  const classes = useStyles();

  const products = useSelector((state) => state.products);
  const totalPrice = useSelector((state) => state.totalPrice);

  const onSubmit = () => {
    const arrayProduct = [];
    if (totalPrice < 3500) {
      alert("la compra debe ser mayor a $3500");
    } else {
      //console.log(products);
      products.map((product) =>
        arrayProduct.push(
          product.product,
          "cantidad:",
          product.quantity,
          "precio:",
          product.total
        )
      );
      arrayProduct.push("TOTAL:", totalPrice);
      //console.log(arrayProduct.join('\n'))

      let url = `https://api.whatsapp.com/send?phone=+5491130762039&text=
    *DISTRIPOP*%0A%0A
    Tu pedido:%0A%0A
    ${arrayProduct}`;
      window.open(url);
    }
  };

  return (
    <TableContainer className={classes.tableMargin} component={Paper}>
      <Table className={classes.table} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>Producto</TableCell>
            <TableCell>Cantidad</TableCell>
            <TableCell align="right"> </TableCell>
            <TableCell align="right"> </TableCell>
            <TableCell align="right">Categoria</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {products.map((product) => (
            <CartList key={product.id} product={product} />
          ))}
        </TableBody>
      </Table>
      <div className={classes.button}>
        <h4>TOTAL: ${totalPrice}</h4>
        <Button
          onClick={() => {
            onSubmit();
          }}
        >
          Pedir por WhatsApp
        </Button>
      </div>
    </TableContainer>
  );
}
